import React from 'react';
import ReactDOM from 'react-dom';
import { Component } from 'react';
import Header from './header';
import Filter from './Filter';
import Listings from './Listings';
import './styles.scss';
import listingsdata from './data/listingsData'


class App extends Component {
  constructor () {
    super()
    this.state = {
      name: 'Steve',
      listingsdata,
      city: 'All',
      homeType: 'All',
      bedrooms: '0',
      min_price: 0,
      max_price: 1000000,
      min_floor_space: 0,
      max_floor_space: 50000,
      elevator: false,
      finished_basement: false,
      gym: false,
      swimming_pool: false,
      filteredData: listingsdata,
      populateFormsData: '',
      sortby: 'price-dsc',
      view: 'box',
      search: ''
    } 
    this.change = this.change.bind(this);
    this.filteredData = this.filteredData.bind(this);
    this.populateForms = this.populateForms.bind(this);
    this.changeView = this.changeView.bind(this);
   }

   UNSAFE_componentWillMount(){
    var listingsdata = this.state.listingsdata.sort((a, b) => {
      return a.price - b.price
    })


     this.setState({
       listingsdata
     })
   }
   change(event) {
     var name = event.target.name;
     var value = (event.target.type === 'checkbox') ? event.target.checked : event.target.value;
     this.setState({
       [name]: value
     }, () => {
       console.log(this.state)
       this.filteredData()
     } )
      console.log(event.target.value);
   }
    changeView(viewName) {
      this.setState({
        view: viewName
      })
    }

   filteredData() {
    var newData = this.state.listingsdata.filter((item) => {
      return item.price >= this.state.min_price && item.price <= this.state.max_price && 
      item.floorspace >= this.state.min_floor_space && item.floorspace <= this.state.max_floor_space && item.rooms >= this.state.bedrooms })

      if(this.state.city !== "All") {
        newData = newData.filter((item) => {
          return item.city === this.state.city
        })
      }

      if(this.state.homeType !== "All") {
        newData = newData.filter((item) => {
          return item.homeType === this.state.homeType
        })
      }

      if(this.state.sortby === 'price-dsc') {
        newData = newData.sort((a,b) => {
          return a.price - b.price
        })
      }

      if(this.state.sortby === 'price-asc') {
        newData = newData.sort((a,b) => {
          return b.price - a.price
        })
      }

      if(this.state.search !== '') {
        newData = newData.filter((item) => {
          var city = item.city.toLowerCase()
          var searchText = this.state.search.toLowerCase()
          var n = city.match(searchText)

          if(n != null) {
            return true
          } return false;
        })
      }

    if(this.state.swimming_pool !== false) {
      newData = newData.filter((item) => {
        return item.extras.includes('swimming_pool') === this.state.swimming_pool
      })
      console.log(newData);
    }

  if(this.state.elevator !== false) {
      newData = newData.filter((item) => {
        return item.extras.includes('elevator') === this.state.elevator
      })
      console.log(newData);
    }

    if(this.state.gym !== false) {
      newData = newData.filter((item) => {
        return item.extras.includes('gym') === this.state.gym
      })
      console.log(newData);
    }

  if(this.state.finished_basement !== false) {
      newData = newData.filter((item) => {
        return item.extras.includes('finished_basement') === this.state.finished_basement
      })
      console.log(newData);
    }
    this.setState({
      filteredData: newData
    })
   }
   populateForms() {
     //city
    var cities = this.state.listingsdata.map((item) => {
      return item.city
    })
    cities = new Set(cities)
    cities = [...cities]
    cities = cities.sort()

     //hometype
     var homeTypes = this.state.listingsdata.map((item) => {
      return item.homeType
    })
    homeTypes = new Set(homeTypes)
    homeTypes = [...homeTypes]
    homeTypes = homeTypes.sort()
     //bedrooms

     var bedrooms = this.state.listingsdata.map((item) => {
      return item.rooms
    })
    bedrooms = new Set(bedrooms)
    bedrooms = [...bedrooms]
    bedrooms = bedrooms.sort()
    this.setState({
      populateFormsData: {
        homeTypes,
        bedrooms,
        cities
      }
    }, () => {
      console.log(this.state);
    }) 

   }

   render() {
   return (
    <div> 
    <Header /> 
    <section id="content-area">
    <Filter change={this.change} globalState={this.state} populateAction={this.populateForms} />
    <Listings listingsdata={this.state.filteredData} change={this.change} globalState={this.state} changeView={this.changeView} />
    </section>
    </div>
  ); 
   }
}

const app = document.getElementById('app');

ReactDOM.render(<App />, app);
